export default {
    data() {  
        return {
            show: false, value:1,
        }
    },
    components: {
        
    },
    beforeCreate(){
        this.$store.dispatch('listadress')
    },
    computed:{
        listadress(){
            return this.$store.state.listadress
        },
        defaultAdress(){
            return this.$store.state.defaultAdress
        },
        CategoryTwoOrThreeListDetail(){
            if(this.$store.state.CategoryTwoOrThreeListDetail == '') this.$store.commit('CategoryTwoOrThreeListDetail')
            return this.$store.state.CategoryTwoOrThreeListDetail
        },
        imgUrlGoods(){
            return this.$store.state.imgUrlGoods
        },
    },
    created(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
        document.title = '订单确认'
        // console.log(12)

        this.$nextTick(()=>{
            this.getAddress()
        })
    },
    activated(){
        // console.log('hah')
    },
    methods:{
        address(){
            this.listadress.length == 0 ? this.$router.push('/AddAddress') : this.show = true
        },
        createdOrder(){
            let order = { 
                addressId: this.defaultAdress.id, orderMoney: (this.CategoryTwoOrThreeListDetail.price*this.value).toFixed(2), orderSource: 0, dataSource: 0, killId:'',
                orderGoods:[ 
                    { 
                        goodsId: this.CategoryTwoOrThreeListDetail.id, goodsName: this.CategoryTwoOrThreeListDetail.goods_name, goodsNum: this.value, nowPrice: this.CategoryTwoOrThreeListDetail.price, 
                        oldPrice: this.CategoryTwoOrThreeListDetail.market_price, thumbPic: this.CategoryTwoOrThreeListDetail.thumbnail_pic 
                    }
                ]
            }
            this.$store.dispatch('createGoods', order)
        },
        details(index){
            this.$router.push({path:'/AddAddress',query:{object:this.listadress[index]}})
        },
        changeAddress(index){
            this.show = false
            this.$store.commit('defaultAdress', this.listadress[index])
        },
        getAddress(){
            
        },
    },
}



